import './App.css';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';
import { Line } from 'react-chartjs-2';
import {useState} from "react";
import Slider from '@mui/material/Slider';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import MySlider from "./components/MySlider";


ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

export const options = {
  responsive: true,
  plugins: {
    legend: {
      position: 'top',
    },
    title: {
      display: true,
      text: 'Wyniki finansowe',
    },
  },
};

const savingMarks = [
  {
    value: 100,
    label: '100 z',
  },
  {
    value: 5000,
    label: '5000 z',
  },
];


function App() {
  const [years, setYears] = useState(20);
  const [monthlySavings, setMonthlySavings] = useState(1000);
  const [yearlyGain, setYearlyGain] = useState(10);

  const labels = [...Array(years+1).keys()]
  const multiplier = 1 + yearlyGain / 100

  const data = {
    labels,
    datasets: [
      {
        label: 'Chowane do skarpety',
        data: labels.map( year => 12 * monthlySavings * year),
        borderColor: 'rgb(255, 99, 132)',
        backgroundColor: 'rgba(255, 99, 132, 0.5)',
      },
      {
        label: `Inwestowane z rocznym zyskiem ${yearlyGain}%`,
        data: labels.map( year => 12 * monthlySavings * (1 - multiplier ** year) / (1 - multiplier)),
        borderColor: 'rgb(53, 162, 235)',
        backgroundColor: 'rgba(53, 162, 235, 0.5)',
      },
    ],
  };


  return (
    <div className="App">
      <Line options={options} value={monthlySavings} data={data} />
      <MySlider label="Miesiczne oszczdnoci"
                min={100}
                max={5000}
                step={100}
                unit=" z"
                value={monthlySavings}
                onChange={setMonthlySavings}
                margin="50px"/>

      <MySlider label="Roczny zysk z inwestycji"
                min={1}
                max={15}
                step={0.5}
                unit="%"
                value={yearlyGain}
                onChange={setYearlyGain}/>

      <MySlider label="Okres oszczdzania"
                min={5}
                max={60}
                step={1}
                unit=" lat"
                value={years}
                onChange={setYears}/>
      </div>
  );
}

export default App;
